@import "../App.scss";

.map-wrap {
  background: rgba(8, 253, 216, 0.1);
  float: right;
  width: 100%;
  height: 100%;
  margin-top: -3%;
}

.leaflet-container {
  width: 100%; 
  height: 80vh;
  opacity: 0;
  animation: backInRight 1s 0.5s;
  animation-fill-mode: forwards;
}

.info-map {
  position: absolute;
  background: #000;
  top: 50px;
  right: 65px;
  z-index: 999999;
  width: 267px;
  padding: 20px;
  color: #fff;
  font-family: "Helvetica";
  font-size: 17px;
  font-weight: 300;
  opacity: 0;
  animation: fadeIn 1s 1.5s;
  animation-fill-mode: forwards;

  span {
    font-size: 16px;
    color: #60c3eb;
  }
}

@include mobile {
    .info-map {
        top: 20px;
        right: 30px;
        z-index: 999999;
        width: 150px;
        padding: 20px;
        color: #fff;
        font-size: 14px;
      
        span {
          font-size: 14px;
        }
    }
}
